import React from "react";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
} from "chart.js";
import { Line } from "react-chartjs-2";
var json_data = require("./data.json");

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

const font_color = "#FFFFFF";
const font_family = '"Console","Fixedsys",monospace';

export const options = {
  responsive: true,
  maintainAspectRatio: false,
  color: font_color,
  interaction: {
    mode: "nearest",
    intersect: false
  },
  stacked: false,
  plugins: {
    title: {
      display: true,
      color: font_color,
      text: "All Gauge Votes"
    },
    legend: {
      position: "bottom",
      labels: {
        font: {
          family: font_family
        }
      }
    }
  },
  scales: {
    y: {
      type: "linear",
      display: true,
      position: "left",
      ticks: {
        color: font_color,
        font: {
          family: font_family
        },
        callback: function (value, index, values) {
          return value + " %";
        }
      }
    },
    x: {
      ticks: {
        font: {
          family: font_family
        },
        color: font_color
      }
    }
  }
};

const labels = json_data.labels;

export const data = {
  labels,
  datasets: json_data.dataset
};

export default function ChartAllGauges() {
  return (
    <>
      <div>
        <Line options={options} data={data} className="responsive-chart" />
      </div>
    </>
  );
}
